import React,{useContext} from "react"
import {NavLink,Route} from "react-router-dom"
import FeatherIcon from 'feather-icons-react';

//Context
import {toggleContext } from "../../../App"

const MenuLink=({lable, to, activeOnlyWhenExact,icon})=>{
  return(
    <Route path={to} exact={activeOnlyWhenExact} children={({match})=>{
      var active =match ? 'pcoded-hasmenu-active' :" pcoded-hasmenu"
      return(
          <li className={active}>
            <NavLink to={to} className="pcoded-link">
              <span className="pcoded-micon">
                <FeatherIcon icon={icon} color="#dcdcdc" size={18} />
              </span>
              <span className="pcoded-mtext">
                  {lable}
              </span>
            </NavLink>
          </li>

      )
    }}/>
  )
}

function mNavbar() {
  // eslint-disable-next-line react-hooks/rules-of-hooks
  const {boolToggle}= useContext(toggleContext);
  if( !boolToggle){
    return (
      <div className="pcoded-navbar-litte">
        <div className="navbar-list">
          <ul className="pcoded-item">
             <MenuLink  to="/dashboard"  activeOnlyWhenExact={true} icon="home" />
             <MenuLink  to="/tables"  activeOnlyWhenExact={false} icon="server" />
             <MenuLink  to="/alarms"  activeOnlyWhenExact={false} icon="bell" />
             <MenuLink  to="/maps"  activeOnlyWhenExact={false} icon="map-pin" />
             <MenuLink  to="/accouts"  activeOnlyWhenExact={false} icon="user" />
          </ul>
        </div>
      </div>
    )
  }
  return (
    <div className="pcoded-navbar">
      <div className="navbar-list">
        <ul className="pcoded-item">
           <MenuLink lable="Dashboard"  to="/dashboard"  activeOnlyWhenExact={true} icon="home" />
           <MenuLink lable="Data Tables"  to="/tables"  activeOnlyWhenExact={false} icon="server" />
           <MenuLink lable="Alarms"  to="/alarms"  activeOnlyWhenExact={false} icon="bell" />
           <MenuLink lable="Maps"  to="/maps"  activeOnlyWhenExact={false} icon="map-pin" />
           <MenuLink lable="Accouts"  to="/accouts"  activeOnlyWhenExact={false} icon="user" />
        </ul>
      </div>
    </div>
  );
}

export default mNavbar;
