import React ,{useReducer}from 'react';
import RouterURL from "./Components/RouterURL"
import { isToggle } from "./Reducers/isToggle"

export const toggleContext = React.createContext();

function App() {
    const [boolToggle, dispatch] = useReducer(isToggle , true);
    return(
      <toggleContext.Provider value={{boolToggle,dispatch}}>
         <RouterURL/>
      </toggleContext.Provider>
    )
}

export default App;
